@import "general.module.scss";

.dashboard {
  color: var(--text_color);
  width: 100vw;

  .dashboard-content {
    margin: 2rem auto;
    min-width: 50%;
    max-width: 80rem;

    border-radius: 0.5rem;
    background-color: var(--contrast_bg_color);

    padding: 2rem;

    h1 {
      margin: 0;
    }

    .name {
      font-family: Hack, Menlo, monospace !important;
      background-color: var(--contrast_bg_color);
      padding: 4px;
      border-radius: 8px;
      margin-right: 4px;
    }

    h2 {
      font-weight: normal;
      margin-bottom: 0.5rem;
    }

    #firstCharts {
      width: 100%;
      display: flex;
      flex-direction: row;
      justify-content: space-evenly;
      gap: 1rem;

      > div {
        padding: 0 1rem;
        width: 50%;
        border: solid 1px var(--disabled_color);
        border-radius: 0.5rem;
      }
    }
  }
}

#offerChart {
  flex: 1;
  height: 20rem;
  min-width: 100%;

  color: black;
}
#requestsChart {
  flex: 1;
  height: 25rem;
  min-width: 100%;

  #tooltip {
    background: var(--contrasterer_bg_color);
    color: var(--text_color);
    padding: 0.5rem;
    border-radius: 0.3rem;
  }
}

#requestChartContainer {
  margin: 1rem 0;
  padding: 0 1rem;
  border: solid 1px var(--disabled_color);
  border-radius: 0.5rem;
}

#stats {
  display: flex;
  justify-content: space-evenly;

  margin: 2rem 0;

  .stat {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;
    width: 100%;

    &:last-child {
      border: none;
    }

    .statValue {
      font-size: 2rem;
      font-weight: bold;

      &.loading {
        background: var(--contrasterer_bg_color);
        height: 1.3em;
        width: 3em;
        border-radius: 0.5rem;
      }
    }

    .statText {
      text-transform: uppercase;
      color: var(--disabled_color);
      font-weight: bold;
      font-size: 0.9rem;
    }

    height: 7rem;
    border-right: solid 1px var(--disabled_color);
  }
}

#growthChartContainer {
  margin: 1rem 0;
  padding: 0 1rem;
  border: solid 1px var(--disabled_color);
  border-radius: 0.5rem;

  #growthChart {
    flex: 1;
    height: 25rem;
    min-width: 100%;

    #tooltip {
      background: var(--contrasterer_bg_color);
      color: var(--text_color);
      padding: 0.5rem;
      border-radius: 0.3rem;
    }
  }
}

#userListContainer {
  #userList {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
    border-radius: 0.5rem;
    .userListItem {
      display: flex;
      justify-content: space-between;
      padding: 1rem;
      border-radius: 0.5rem;
      background-color: var(--contrasterer_bg_color);

      h1 {
        font-size: 1.4rem;
        font-weight: normal;
      }

      p {
        margin: 0.3rem 0;
      }

      button {
        height: 2rem;
        padding: 0 1rem;
        color: white;
        border: none;
        font-size: inherit;
        white-space: nowrap;
        cursor: pointer;
        border-radius: 5px;

        &:enabled {
          background-color: $error_color;
        }

        &:disabled {
          background-color: var(--disabled_color);
        }
      }
    }
  }
}

