#ipTable {
  width: 100%;
  thead {
    font-weight: bold;
  }
  p {
    margin: 0 auto;
  }
}

@media screen and (max-width: 1400px) {
  #firstCharts {
    flex-direction: column !important;
    align-items: center;

    > div {
      width: 100% !important;
    }
  }
}

@media screen and (max-width: 1000px) {
  #userList {
    grid-template-columns: 1fr;
  }
}
